import { Component, Element, Host, h, Prop, Method } from '@stencil/core';
import type { IconName } from '@fortawesome/fontawesome-svg-core';
import { submitForm } from '../../internal/forms';
import { createFocusHandler } from '../../internal/a11y';

@Component({
	tag: 'o-icon-button',
	styleUrl: 'o-icon-button.scss',
	shadow: true
})
export class OIconButton {
	@Element() host!: HTMLOIconButtonElement;

	/** Indicates whether the button is disabled, preventing user interaction. */
	@Prop({ reflect: true }) disabled?: boolean = false;

	/** The name of the icon to be rendered. */
	@Prop({ reflect: true }) name?: IconName;

	/** The size of the icon, can be 'sm', 'md', 'lg'. */
	@Prop({ reflect: true }) size: 'sm' | 'md' | 'lg' = 'sm';

	/** A description that gets read by assistive devices. */
	@Prop({ reflect: true }) label?: string = '';

	/** Optional color for the icon, can be a hex value or a color name. */
	@Prop({ reflect: true }) color?: string = 'main_text';

	/** The type of the button, can be "button", "submit", "reset". */
	@Prop() type: 'button' | 'submit' | 'reset' = 'submit';

	/** Reference to the internal button element */
	private buttonEl: HTMLButtonElement;

	// Create focus handler
	private focusHandler = createFocusHandler(this, () => this.buttonEl);

	/** Handles click events on the button or link. */
	private handleClick = (event: MouseEvent) => {
		if (this.disabled) {
			event.preventDefault();
			event.stopPropagation();
			return;
		}
		event.preventDefault();
		submitForm(this.host);
	};

	/**
	 * Runs when the component is connected to the DOM
	 */
	connectedCallback() {
		// Setup focus handling
		this.focusHandler.setupFocusHandling();
	}

	/**
	 * Runs when the component is disconnected from the DOM
	 */
	disconnectedCallback() {
		// Clean up focus handling
		this.focusHandler.cleanupFocusHandling();
	}

	/**
	 * Public method to focus the button
	 * The name is setFocus() to avoid conflicts with the native focus() method
	 */
	@Method()
	async setFocus(): Promise<void> {
		return this.focusHandler.setFocus();
	}

	render() {
		const btnClassName =
			'btn px-2 py-2 bg-transparent shadow-none text-main_text hover:text-accent_blue_1 active:text-main_text focus-visible:outline focus-visible:outline-[3px] focus-visible:outline-accent_blue_1 focus-visible:outline-offset-0';

		// Add a class to indicate the button is disabled
		const disabledClass = this.disabled ? 'btn cursor-not-allowed bg-transparent text-blue_steel shadow-none' : '';

		return (
			<Host>
				<button
					class={`${this.disabled ? disabledClass : btnClassName} ${disabledClass}`}
					disabled={this.disabled}
					onClick={this.handleClick}
					aria-label={this.label}
					type={this.type}
					ref={(el) => (this.buttonEl = el as HTMLButtonElement)}
					tabIndex={0}
				>
					<span class="flex items-center gap-2">
						<o-icon name={this.name} size={this.size} color={this.color}></o-icon>
					</span>
				</button>
			</Host>
		);
	}
}
